import React, { Fragment, useState } from "react";
import axios from "axios";
import { Container, Alert, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { REGISTER_SUCCESS, REGISTER_FAIL } from "../../actions/types"

const Signup = () => {
    const isAuthenticated = useSelector((state) => state.auth.isAuthenticated);
    const profile = useSelector((state) => state.profile);
    const [code, setCode] = useState('')
    const [email, setEmail] = useState('')
    const [name, setName] = useState('')
    const [password, setPassword] = useState('')
    const [error, setError] = useState(null)
    let history = useHistory();
    const dispatch = useDispatch();
    
    const handleSignup = () => {
        console.log({
            code: code,
            name: name,
            email: email,
            password: password
        }); 

        if(code == "ANTLER2021"){
            axios.post("/api/register", {
                code: code,
                name: name,
                email: email,
                password: password
            }).then((res) => {
                dispatch({
                    type:REGISTER_SUCCESS,
                    payload:{
                     profile: res.data
                    }});
                console.log(res)
                history.push("/profile")
                
            }
            ).catch((err) => {
                console.log(err)
                setError("Opps! User already exist!");
            });
        }else{
            setError("Opps! Wrong code!");
        }
        

    }

	return (
        <Container style={{background: "#FAD981"}}>

<div className="d-flex justify-content-center pb-5">
		<img className="mt-5 mb-5" src="https://res.cloudinary.com/worthmore/image/upload/v1614288611/TechChallenge/logo.svg" />

        </div>
        <div className="text-center pb-5">
        {
            error ?  <Alert block color="danger">
            {error}
          </Alert> : null
        }
       
        <Form>
        <FormGroup>
            <Label for="exampleEmail">Invite Code</Label>
            <Input style={{borderRadius:"40px"}}  onChange={event => setCode(event.target.value)} type="code" name="code" id="code" placeholder="Enter your invite code" />
        </FormGroup>
        <FormGroup>
            <Label for="exampleEmail">Name</Label>
            <Input style={{borderRadius:"40px"}}  onChange={event => setName(event.target.value)} type="name" name="name" id="name" placeholder="Whats your name?" />
        </FormGroup>
        <FormGroup>
            <Label for="exampleEmail">Email</Label>
            <Input style={{borderRadius:"40px"}}  onChange={event => setEmail(event.target.value)} type="email" name="email" id="email" placeholder="Whats your email?" />
        </FormGroup>
        <FormGroup>
            <Label for="examplePassword">Password</Label>
            <Input style={{borderRadius:"40px"}}  onChange={event => setPassword(event.target.value)} type="password" name="password" id="password" placeholder="Choose a super secure password" />
        </FormGroup>

        <Button className="mt-5 mb-5" style={{borderRadius:"40px",background:"#575757"}} onClick={()=>handleSignup()} block>Signup</Button>
        <a className="text-center mt-5 pt-5 pb-5 mb-5" style={{color:"#000"}} onClick={()=> history.push("/login")}>Login here!</a>
      </Form>
      </div>
    </Container>
	);
};

export default Signup;
