import {
	USER_LOADED,
	USER_LOADING,
	AUTH_ERROR,
	LOGIN_SUCCESS,
	LOGIN_FAIL,
	LOGOUT_SUCCESS,
	REGISTER_SUCCESS,
    REGISTER_FAIL,
    USER_UPDATED,
    GET_PROFILES,
    SET_RESULT,
    SET_TURNS
} from "../actions/types";

const initialState = {
	isAuthenticated: null,
	isLoading: false,
	profile: null,
    profiles: null,
    turns: 10, 
    result: 0
};

export default function (state = initialState, action) {
	switch (action.type) {
		case USER_LOADING:
			return {
				...state,
				isLoading: true,
			};
		case USER_LOADED:
			return {
				...state,
				isAuthenticated: true,
				isLoading: false,
				profile: action.payload.user,
			};
		case GET_PROFILES:
			return {
				...state,
				profiles: action.payload,
			};
		case SET_RESULT:
			return {
				...state,
				result: action.payload,
			};
		case SET_TURNS:
			return {
				...state,
				turns: action.payload,
			};
		case LOGIN_SUCCESS:
		case REGISTER_SUCCESS:
			return {
				...state,
				...action.payload,
				isAuthenticated: true,
				isLoading: false,
			};
		case USER_UPDATED:
			return {
				...state,
				...action.payload,
				isAuthenticated: true,
				isLoading: false,
			};
		case AUTH_ERROR:
		case LOGIN_FAIL:
		case LOGOUT_SUCCESS:
		case REGISTER_FAIL:
			return {
				...state,
				user: null,
				company: null,
				isAuthenticated: false,
				isLoading: false,
			};
		default:
			return state;
	}
}
