import React, { Fragment } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Container, Col, Row, Input, Button, Form, FormGroup, Label, Progress } from 'reactstrap';
import { useHistory } from "react-router-dom";
import { SET_RESULT, SET_TURNS } from "../../actions/types"

const Result = () => {
    const dispatch = useDispatch();
    const result = useSelector((state) => state.auth.result);
    const turns = useSelector((state) => state.auth.turns);
    let history = useHistory();

    const handlePlayAgain = () =>{
        dispatch({
            type: SET_TURNS,
            payload: 10
        })
        dispatch({
            type: SET_RESULT,
            payload: 0
        })
        history.push("/play")
    }

	return (
        <Container className="text-center pb-5" style={{background: "#FAD981"}}>
<div className="d-flex justify-content-center pb-5">
		<img className="mt-5" src="https://res.cloudinary.com/worthmore/image/upload/v1614288611/TechChallenge/logo.svg" />

        </div>

        <h2 className="text-center mt-5 mb-5">{result/turns >0.5 ? "Congrats! :)" : "Ooops! You need to practise more!:)" }</h2>
        <Form>
        <FormGroup tag="fieldset">
        <legend className="text-center mt-3 mb-3">You got {result} correct answers</legend>
        
        <div className="text-center">{result} of {turns}</div>
      <Progress value={result} max={turns} />
        
       
      </FormGroup>
      <p>Did You know your personality type matches well with Magnus, maybe you should reach out for a coffee</p>
      <Button onClick={()=> handlePlayAgain()} className="mt-5" style={{background: "#575757", borderRadius:"40px", color:"#fff", fontWeight:"700"}} block>Try again</Button>
        </Form>
        </Container>
        
	);
};

export default Result;
