import React, { useState, Fragment } from "react";
import axios from "axios";
import logo from './logo.svg';
import './App.css';
import Login from "./components/Auth/Login";
import SignUp from "./components/Auth/Signup";
import Profile from "./components/Profile/Profile";
import Home from "./components/Home/Home";
import MBTIGame from "./components/MBTIGame/MBTIGame";
import result from "./components/MBTIGame/MBTIResult";
import { Switch, Route } from "react-router-dom";

import {
  Container,
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Button
  
} from 'reactstrap';

const NoMatchPage = () => {
	return (
		<div className="h-100 w-100 text-center">
			<h3>404 - The page does not exist</h3>
		</div>
	);
};
axios.defaults.baseURL = process.env.REACT_APP_AXIOS_BASE_URL;

function App() {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);
  return (
    <Container>
<Fragment>

    <Switch>
			<Route path="/play" component={MBTIGame} />
			<Route path="/result" component={result} />
			<Route path="/profile" component={Profile} />
			<Route path="/login" component={Login} />
			<Route path="/signup" component={SignUp} />
			<Route path="/" exact component={Login} />
			<Route component={NoMatchPage} />
		</Switch>
</Fragment>
    </Container>
    
    
  );
}

export default App;
