export const ProfileComponent = ({profile}) => {
    const dispatch = useDispatch();
    const [randomPersonalities, setRandomPersonalities] = useState([])
    const [selected, setSelected] = useState()
    const [message, setMessage] = useState(null)
    const result = useSelector((state) => state.auth.result);
    
    function getRandomInt(max) {
        return Math.floor(Math.random() * Math.floor(max));
      }

      function shuffle(array) {
        var tmp, current, top = array.length;
        if(top) while(--top) {
          current = Math.floor(Math.random() * (top + 1));
          tmp = array[current];
          array[current] = array[top];
          array[top] = tmp;
        }
        return array;
      }

    useEffect(()=>{
        
        if(selected === profile.mbti){
            console.log("correct");
            setMessage("Correct, "+profile.name+" is an "+profile.mbti)
            dispatch({
                type: SET_RESULT,
                payload: result+1
            })
               
        } else {
            console.log("Wrong");
            setMessage(profile.name+" is actully a "+profile.mbti)
        }
    }, [selected])

    useEffect(()=>{
        setMessage(null);
        

        console.log(personalities)
        const types = [
            {value: profile.mbti} 

        ]

        for (var i = 0; i < 3; i++) {
            types.push(
                personalities[getRandomInt(15)]
            )

        }
        
        const shulledTypes = shuffle(types);
        setRandomPersonalities(shulledTypes);
        console.log(shulledTypes);
          
        
    }, [profile])


	return (
        

		
        <Form>
        
        
        
             
        <FormGroup tag="fieldset">
        <p className="text-center">Select which personality type you feel best suits your cohort member</p>
<Row>
        {randomPersonalities.map(type => 

<Col key={getRandomInt(1000000)}  xs="6" className="p-3">
<div className="text-center" style={{cursor: "pointer"}} onClick={()=>setSelected(type.value)}>

<img src={"https://res.cloudinary.com/worthmore/image/upload/v1614288287/TechChallenge/"+type.value+".svg"} />
</div>
</Col>


            
           
            
            
            )}
        </Row>
        
        
        
       <div className="text-center">
           <p >Cohort member:</p>
           <div style={{background: "#E8CC80", borderRadius:"40px", padding: "10px"}}>{profile && profile.name ? profile.name : null} </div>
       </div>
            
      </FormGroup>
      {message ? <p className="text-center">{message}</p> : null}
          
      
        </Form>
       
        
	);
};

