import React, {Fragment, useState, useEffect} from 'react';
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { Container, Col, Row, Alert, Input, Button, Form, FormGroup, Label, Progress } from 'reactstrap';
import axios from "axios";
import { GET_PROFILES, SET_RESULT, SET_TURNS } from "../../actions/types"
import { ProfileComponent } from "./ProfileComponent"

const MBTIGame = () => {
    const dispatch = useDispatch();
    const [turn, setTurn] = useState(1)
    let history = useHistory();
    const isAuthenticated = useSelector((state) => state.auth.isAuthenticated);
    const profile = useSelector((state) => state.auth.profile);
    const result = useSelector((state) => state.auth.result);
    const profiles = useSelector((state) => state.auth.profiles);
    const [noOfTurns, setNoOfTurns] = useState(10)


    useEffect(() =>{
        axios.get("/api/profiles").then((res) => {
            dispatch({
                type:GET_PROFILES,
                payload:
                 res.data
                });
            console.log(res.data.length)
            if(res.data.length<10)setNoOfTurns(res.data.length);
            
            
            
        }
        ).catch((err) => {
            console.log(err)
            
        });
        

    }, [])


    const handleNext = () => {
        setTurn(turn+1);
        if(turn === noOfTurns) {
            dispatch({
                type: SET_TURNS,
                payload: noOfTurns
            })
            dispatch({
                type: SET_RESULT,
                payload: result
            })
            history.push("/result") 
        }
            

    }

	return (
        <Container>
            <Row className="d-flex justify-content-center pt-2 pb-3">
                <img widht="40" src="https://res.cloudinary.com/worthmore/image/upload/v1614288828/TechChallenge/logo-text.svg" />
            </Row>
		
        <Form>
        
         <Progress color="info" value={turn} max={noOfTurns} />
         <div className="text-center">{turn} of {noOfTurns}</div>
         <br/>

        {profiles ? <ProfileComponent profile={profiles[turn-1]} /> : null}
        
        <div className="d-flex justify-content-center">

        <Button className="mt-2" onClick={()=>handleNext()} style={{background:"#575757", color:"#fff", borderRadius:"40px"}}>Next</Button>
        </div>
        </Form>
        </Container>
        
	);
};

export default MBTIGame;
