import React, { Fragment, useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { Image, Container, Alert, Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import { LOGIN_SUCCESS } from "../../actions/types"

const Login = () => {
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [error, setError] = useState(null)
    let history = useHistory();
    const dispatch = useDispatch();

    const handleLogin = () => {
        console.log({
            email: email,
            password: password
        }); 

        axios.post("/api/login", {
            email: email,
            password: password
        }).then((res) => {
            dispatch({
                type:LOGIN_SUCCESS,
                payload:{
                 profile: res.data
                }});
            console.log(res)
            history.push("/profile")
            
        }
        ).catch((err) => {
            console.log(err)
            setError("Opps! Wrong username or password");
        });


    }
	return (
        <Container style={{background: "#FAD981"}}>
        <div className="d-flex justify-content-center pb-5">
		<img className="mt-5 mb-5" src="https://res.cloudinary.com/worthmore/image/upload/v1614288611/TechChallenge/logo.svg" />

        </div>
        <div className="text-center pb-5">

        
        {
            error ?  <Alert block color="danger">
            {error}
          </Alert> : null
        }
        <Form>
        <FormGroup>
            <Label for="exampleEmail">Email</Label>
            <Input className="p-4" style={{borderRadius:"40px"}} onChange={event => setEmail(event.target.value)} type="email" name="email" id="email" placeholder="Whats your email" />
        </FormGroup>
        <FormGroup>
            <Label for="examplePassword">Password</Label>
            <Input className="p-4" style={{borderRadius:"40px"}} onChange={event => setPassword(event.target.value)} type="password" name="password" id="password" placeholder="Enter your password" />
        </FormGroup>

        <Button className="mt-5 mb-5" style={{background: "#575757", borderRadius:"40px"}} onClick={()=>handleLogin()} block>Login</Button>
      <a className="text-center pb-5 mt-5 mb-5" style={{color:"#000"}} onClick={()=> history.push("/signup")}>Sign up here!</a>
      </Form>
      </div>
    </Container>
	);
};

export default Login;
